import React, { Component } from 'react'
import Endereco from './Endereco'
import ResponsavelLegal from './ResponsavelLegal'

export default class Aluno extends Component {

    constructor(props){
        super(props)

        this.state = {
            nome : '',
            matricula : '',
            cpf : '',
            data_nascimento : '',
            telefone : '',
            email : '',
            curso : {},
            endereco : {
                cep : '',
                logradouro : '',
                bairro : '',
                cidade : '',
                uf : '',
            },
            responsavel_legal : {
                nome : '',
                cpf : '',
                parentesco : '',
                telefone : '',
                email : '',
            },
        }

        this.onChange = this.onChange.bind(this)
    }

    componentDidMount(){
        const {dados} = this.props

        if(dados !== undefined){
            const {aluno} = this.props.dados
        
            if(aluno !== undefined){
                this.setState(prevState => ({...prevState, ...aluno}) )
            }
        }
    }

    componentWillUnmount(){
        this.props.salvaDados('aluno', this.state)
    }
    
    onChange(e) {
        const campo = e.target
        this.setState({[campo.name]: campo.value})
    }

    maiorIdade(){
        const {data_nascimento} = this.state

        if(data_nascimento === undefined || data_nascimento === '') return true

        // instancia a data em um objeto Date
        const dataDeNascimento = new Date(data_nascimento)

        // extrai dia, mes e ano da data informada
        const dia = dataDeNascimento.getDate();
        const mes = dataDeNascimento.getMonth();
        const ano = dataDeNascimento.getFullYear();

        // compara a data informada + 18 anos (dia+1 por iniciar em 0) com a data atual e verifica se há um intervalo maior ou igual a 18 anos
        const maiorIdade = new Date(ano+18, mes, dia+1) <= new Date()

        // persiste o resultado da comparacao no state
        return maiorIdade
    }

    render() {
        const { 
            nome,
            nome_social,
            matricula,
            cpf,
            curso,
            data_nascimento,
            telefone,
            email,
            endereco,
            responsavel_legal,
        } = this.state

        const {adicionaExtra, extras} = this.props
        const {listaDeCursos} = extras

        return (
            <div>
                
                <div className="bg-light">
                    <h2 className="my-0 text-dark">Dados do Aluno</h2>
                </div>

                <div className="form-row">
                    <div className="form-group col-lg-2 col-md-3 col-sm-12 col-12">
                        <label htmlFor="matricula">Nº de Matrícula</label>
                        <input className="form-control" id="matricula" name="matricula" type="text" value={matricula} onChange={this.onChange} required/>
                    </div>
                
                    <div className="form-group col-lg-2 col-md-3 col-sm-12 col-12">
                        <label htmlFor="cpf">CPF</label>
                        <input className="form-control" id="cpf" name="cpf" type="text" value={cpf} onChange={this.onChange} required/>
                    </div>

                    <div className="form-group col-lg-8 col-md-6 col-sm-12 col-12">
                        <label htmlFor="curso">Curso</label>
                        <select 
                            className="custom-select"
                            value={curso.id}
                            onChange={ (e) => {
                                const id = e.target.value
                                this.setState({ curso : {id} })
                                adicionaExtra("cursoSelecionado", id)
                                adicionaExtra("carregaLocaisDeEstagio", true)
                            }}
                            required
                        >
                            {
                                (listaDeCursos === undefined || listaDeCursos.length === 0) ?
                                    <option key='' value=''>Carregando lista de cursos...</option>    
                                :   
                                    <option key='false' value=''>Qual o seu curso?</option>
                            }
                            {
                                listaDeCursos.map(curso => <option key={curso.id} value={curso.id}>{curso.nome}</option>)
                            }
                        </select>
                    </div>

                    <div className="form-group col-lg-12 col-md-12 col-sm-12 col-12">
                        <label htmlFor="nome">Nome Completo</label>
                        <input className="form-control" id="nome" name="nome" type="text" value={nome} onChange={this.onChange} required/>
                    </div>

                    <div className="form-group col-lg-12 col-md-12 col-sm-12 col-12">
                        <label htmlFor="nome_social">Nome Social</label>
                        <input className="form-control" id="nome_social" name="nome_social" type="text" value={nome_social} onChange={this.onChange} required/>
                    </div>
                </div>

                
                <div className="form-row">
                    <div className="form-group col-lg-3 col-md-3 col-sm-5 col-12">
                        <label htmlFor="data_nascimento">Data de Nascimento</label>
                        <input className="form-control" id="data_nascimento" name="data_nascimento" type="date" value={data_nascimento} onChange={this.onChange} required/>
                    </div>

                    <div className="form-group col-lg-3 col-md-3 col-sm-7 col-12">
                        <label htmlFor="telefone">Telefone</label>
                        <input className="form-control" id="telefone" name="telefone" type="text" value={telefone} onChange={this.onChange} required/>
                    </div>

                    <div className="form-group col-lg-6 col-md-6 col-sm-12 col-12">
                        <label htmlFor="email">Email</label>
                        <input className="form-control" id="email" name="email" type="email" value={email} onChange={this.onChange}/>
                    </div>        
                </div>

                <Endereco onChange={this.onChange} value={endereco}/>
                { !this.maiorIdade() ? <ResponsavelLegal onChange={this.onChange} value={responsavel_legal} /> : '' }
            </div>
        );
    }
}