import React from 'react'

export default class Formulario extends React.Component {
    constructor(){
        super()
        this.state = {
            tela : {
                atual : null,
                anterior : null,
                proxima : null,
            },
            dados:{},
        }

        this.telaAnterior = this.telaAnterior.bind(this)
        this.telaSeguinte = this.telaSeguinte.bind(this)

        this.salvaDados = this.salvaDados.bind(this)
        this.processaFormSubmit = this.processaFormSubmit.bind(this)
    }

    componentWillMount(){
        this.setState(prevState => {
            const tela = {
                ...prevState.tela,
                anterior : 0,
                atual : 0,
                proxima : 1,
            }

            return {tela}
        })
    }

    telaSeguinte(){
        const {telas} = this.props

        this.setState(prevState => {
            const {atual} = prevState.tela

            if(atual <= telas.length){
                const tela = {
                    ...prevState.tela,
                    anterior : atual,
                    atual : atual+1,
                    proxima : atual+2,
                }

                return {tela}
            }
        })
    }

    telaAnterior(){
        this.setState(prevState => {
            const {atual} = prevState.tela

            if(atual > 0){
                const tela = {
                    ...prevState.tela,
                    anterior : atual-2,
                    atual : atual-1,
                    proxima : atual,
                }

                return {tela}
            }
        })
    }

    _getTela(){
        const {telas, telaPreSubmit, telaPostSubmit} = this.props
        const {atual} = this.state.tela

        const TelaPreSubmit = telaPreSubmit !== undefined ? telaPreSubmit : TelaPreSubmitPadrao
        const TelaPostSubmit = telaPostSubmit !== undefined ? telaPostSubmit : TelaPostSubmitPadrao

        const qtdTelas = telas.length

        if(atual >= 0 && atual < qtdTelas){
            return telas[atual]
        }
        else if(atual === qtdTelas){
            return TelaPreSubmit
        }

        else if(atual > qtdTelas){
            return TelaPostSubmit
        }
        
        return telas[0]
    }

    salvaDados(elemento, dados){
        this.setState(prevState => (
            {
                dados : {
                   ...prevState.dados,
                   [elemento] : dados
                }
            }
        ))
    }

    processaFormSubmit(e){
        e.preventDefault();

        const {atual} = this.state.tela
        const {telas} = this.props

        if(atual >= telas.length){
            const submitResult = this.props.onSubmit(this.state.dados)
            this.setState({submitResult})

            this.telaSeguinte()
        }
        else if(atual >= 0){
            this.telaSeguinte();
        }
    }

    render(){
        const Tela = this._getTela()
        const telaAtual = this.state.tela.atual
        const totalDeTelas = this.props.telas.length
        const {dados, submitResult} = this.state
        const {extras} = this.props
        
        return (
            <form onSubmit={this.processaFormSubmit}>
                <Tela salvaDados={this.salvaDados} dados={dados} submitResult={submitResult} {...this.props}/>

                <input
                    type="button"
                    value="Voltar"
                    hidden={telaAtual <= 0 || (telaAtual > totalDeTelas && !extras.falhaNoEnvio) }
                    onClick={() => this.telaAnterior()}
                />

                <input
                    type="submit"
                    value={telaAtual >= totalDeTelas ? "Enviar" : "Avançar"}
                    hidden={telaAtual > totalDeTelas }
                />
            </form>
        )
    }
}

const TelaPreSubmitPadrao = () => (
    <div>
        <h1>Finalizando...</h1>
        <p>Basta clicar no botão enviar para finalizarmos</p>
    </div>
)

const TelaPostSubmitPadrao = () => (
    <div>
        <h1>Enviado!</h1>
        <p>Obrigado :)</p>
    </div>
)