import React from 'react'

export default class Supervisor extends React.Component {
    constructor(){
        super()

        this.onChange = this.onChange.bind(this)
    }

    onChange(e){
        const {name, value} = e.target

        const target = {
            name : "supervisor",
            value : {
                ...this.props.dados,
                [name] : value
            }
        }
        this.props.onChange({target})
    }

    render(){
        const {nome, cargo, formacao, telefone, email} = this.props.dados

        return (
            <div>
                <div className="bg-light py-2">
                    <h2 className="my-0 text-dark">Quem será o supervisor do estágio no local escolhido?</h2>
                </div>

                <div className="form-row">
                    <div className="form-group col-lg-7 col-md-12 col-sm-12 col-12">
                        <label htmlFor="nome">Nome Completo</label>
                        <input className="form-control" id="nome" name="nome" type="text" value={nome} sessao="teste" onChange={this.onChange} required/>
                    </div>

                    <div className="form-group col-lg-5 col-md-5 col-sm-12 col-12">
                        <label htmlFor="cargo">Cargo</label>
                        <input className="form-control" id="cargo" name="cargo" type="text" value={cargo} onChange={this.onChange} required/>
                    </div>
                
                    <div className="form-group col-lg-4 col-md-7 col-sm-12 col-12">
                        <label htmlFor="formacao">Formação</label>
                        <input className="form-control" id="formacao" name="formacao" type="text" value={formacao} onChange={this.onChange} required/>
                    </div>

                    <div className="form-group col-lg-2 col-md-4 col-sm-12 col-12">
                        <label htmlFor="telefone">Telefone</label>
                        <input className="form-control" id="telefone" name="telefone" value={telefone} onChange={this.onChange} required/>
                    </div>

                    <div className="form-group col-lg-6 col-md-8 col-sm-12 col-12">
                        <label htmlFor="email">Email</label>
                        <input className="form-control" type="email" id="email" name="email" value={email} onChange={this.onChange}/>
                    </div>
                </div>
            </div>
        )
    }
}