import React from 'react'

export default class ResponsavelLegal extends React.Component {

    constructor(){
        super()
        this.onChange = this.onChange.bind(this)
    }

    onChange(e){
        const {name, value} = e.target

        const target = {
            name : "responsavel_legal",
            value : {
                ...this.props.value,
                [name] : value
            }
        }

        this.props.onChange({target})
    }

    render(){
        const {nome, cpf, parentesco, telefone, email} = this.props.value

        const {hidden} = this.props

        const onChange = this.onChange

        return(
            <div hidden={hidden}>
                <div className="bg-light">
                    <h2 className="my-0 text-dark">Representante Legal</h2>
                </div>
    
                <div className="form-row">
                    <div className="form-group col-lg-10 col-md-12 col-sm-12 col-12">
                        <label htmlFor="nome">Nome Completo</label>
                        <input className="form-control" id="nome" name="nome" type="text" value={nome} onChange={onChange} required/>
                    </div>
    
                    <div className="form-group col-lg-2 col-md-3 col-sm-5 col-12">
                        <label htmlFor="cpf">CPF</label>
                        <input className="form-control" id="cpf" name="cpf" type="text" value={cpf} onChange={onChange} required/>
                    </div>
    
                    <div className="form-group col-lg-3 col-md-9 col-sm-7 col-12">
                        <label htmlFor="parentesco">Parentesco</label>
                        <input className="form-control" id="parentesco" name="parentesco" type="text" value={parentesco} onChange={onChange} required/>
                    </div>
    
                    <div className="form-group col-lg-3 col-md-4 col-sm-12 col-12">
                        <label htmlFor="telefone">Telefone</label>
                        <input className="form-control" id="telefone" name="telefone" type="text" value={telefone} onChange={onChange} required/>
                    </div>
    
                    <div className="form-group col-lg-6 col-md-8 col-sm-12 col-12">
                        <label htmlFor="email">Email</label>
                        <input className="form-control" id="email" name="email" type="email" value={email} onChange={onChange} />
                    </div>
                </div>
            </div>
        )
    }
}